.range-selector {
  margin-bottom: 32px;
  input {
    width: 100%;
    margin-top: 20px;
  }
  input[type='range'] {
    width: 100%;
    background-color: transparent;
    -webkit-appearance: none;
  }
  input[type='range']:focus {
    outline: none;
  }
  input[type='range']::-webkit-slider-runnable-track {
    background: var(--main-clr);
    border: 0.2px solid rgba(0, 1, 1, 0);
    width: 100%;
    height: 4px;
    cursor: pointer;
  }
  input[type='range']::-webkit-slider-thumb {
    margin-top: -8px;
    width: 32px;
    height: 18px;
    background: #ffffff;
    border: var(--border-width) solid #000000;
    cursor: pointer;
    -webkit-appearance: none;
  }
  input[type='range']:focus::-webkit-slider-runnable-track {
    background: var(--main-clr);
  }
  input[type='range']::-moz-range-track {
    background: var(--main-clr);
    border: 0.2px solid rgba(0, 1, 1, 0);
    width: 100%;
    height: 4px;
    cursor: pointer;
  }
  input[type='range']::-moz-range-thumb {
    width: 32px;
    height: 18px;
    background: #ffffff;
    border: var(--border-width) solid #000000;
    cursor: pointer;
  }
  input[type='range']::-ms-track {
    background: transparent;
    border-color: transparent;
    border-width: 5px 0;
    color: transparent;
    width: 100%;
    height: 4px;
    cursor: pointer;
  }
  input[type='range']::-ms-fill-lower {
    background: var(--main-clr);
    border: 0.2px solid rgba(0, 1, 1, 0);
  }
  input[type='range']::-ms-fill-upper {
    background: var(--main-clr);
    border: 0.2px solid rgba(0, 1, 1, 0);
  }
  input[type='range']::-ms-thumb {
    width: 32px;
    height: 18px;
    background: #ffffff;
    border: var(--border-width) solid #000000;
    cursor: pointer;
    margin-top: 0px;
  }
  input[type='range']:focus::-ms-fill-lower {
    background: var(--main-clr);
  }
  input[type='range']:focus::-ms-fill-upper {
    background: var(--main-clr);
  }
}

.checkbox-block {
  display: flex;
  flex-direction: column;
  padding: 0 0 10px;
  input[type='checkbox'] {
    display: none;
    & + label {
      display: flex;
      align-items: center;
      position: relative;
      cursor: pointer;
      padding: 0;
    }
    & + label:before {
      content: '';
      position: relative;
      // margin-top: 16px;
      margin-right: 18px;
      display: inline-block;
      width: 24px;
      height: 24px;
      background: white;
      border: var(--border-width) solid var(--text-clr-light);
    }
  }
  input[type='checkbox']:checked {
    & + label {
      &:before {
        background: var(--main-clr);
      }
      &:after {
        content: '';
        width: 20px;
        height: var(--border-width);
        background: var(--secondary-clr);
        display: inline-block;
        position: absolute;
        bottom: 11px;
        left: 2px;
        transform: rotate(-45deg);
      }
    }
  }
}

//////
.switcher {
  z-index: 999;
  position: absolute;
  top: 16px;
  right: 16px;
  input[type='checkbox'] {
    display: none;
    & + label {
      text-transform: uppercase;
      display: flex;
      align-items: center;
      position: relative;
      cursor: pointer;
      padding: 0;
      color: var(--secondary-clr);
    }
    & + label:after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: -42px;
      display: inline-block;
      width: 14px;
      height: 20px;
      background: white;
      border: var(--border-width) solid var(--secondary-clr);
      transition: all 0.08s ease-in-out;
    }
    & + label:before {
      content: '';
      position: absolute;
      left: -42px;
      margin-right: 10px;
      display: inline-block;
      width: 32px;
      height: 20px;
      background: var(--main-clr);
      border: var(--border-width) solid var(--secondary-clr);
    }
  }
  input[type='checkbox']:checked {
    & + label {
      &:after {
        left: -24px;
      }
    }
  }
}

.secondary-btn {
  @include btn-mixin(var(--secondary-clr), white, var(--secondary-clr));
  border: var(--border-width) solid var(--main-clr);
  color: #111;
  &.active {
    background: var(--main-clr);
    border: var(--border-width) solid var(--main-clr);
    color: var(--text-clr-dark);
    &:hover {
      background: var(--main-clr) !important;
      border: var(--border-width) solid var(--text-clr-dark) !important;
      color: var(--secondary-clr) !important;
    }
  }
}
